<template>
<div>
    <app-header></app-header>
    
  <div class="center" style="margin-top: 200px;">
      <div class="col-md-4">
        <loader v-if="showLoader"></loader>
        
        <div class="col-12" style="padding-top: 10px;">
            <input type="text" v-model="code" class="form-control reset-border" placeholder="Enter Code">
        </div>
        <div class="col-12">
            <div style="text-align: center;color: red;" v-if="isIncorrectCode">Incorrect Code</div>
        </div>
        <div class="col-12">
            <button @click="validateCode" style="margin-top: 10px;" class="btn btn-primary btn-block reset-border" :disabled="disable">START</button>
        </div>
      </div>
  </div>
</div>
</template>

<script>
import axios from 'axios';
import Loader from './Loader';
import Header from './Header';


export default {
    components:{
        Loader,     
       appHeader:Header,
    
    },
    data(){
        return{
            code: '',
            isIncorrectCode: false,
            showLoader: false,
            quiz: ''
        }
    },methods:{
        validateCode: function(){
            //console.log(this.code);

            this.showLoader = true;
            axios.get('/static/json/questions.json')
            .then((response) => {
                //console.log(response.data);

                for(let data of response.data){
                    if(data.code == this.code){
                        this.quiz = data.quiz;

                        this.$router.push({name: 'Poll', params:{quizData: this.quiz}})
                        break;
                    }
                }

                if(this.quiz == ''){
                    this.isIncorrectCode = true;
                }

                this.showLoader = false;
                console.log(this.quiz);

            })
            .catch((error) => {
                console.log(error);
                this.showLoader = false;
            });

        }
    },
    computed:{
        'disable': function(){
            if(this.code.length < 4){
                return true;
            }

            return false;
        }
    }

}
</script>

<style>

    .center{
        display: flex;
        justify-content: center;
    }

    .reset-border{
        border-radius: 0;
    }

</style>



