import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Admin from '@/components/Admin'
import Quiz from '@/components/Quiz'
import Poll from '@/components/Poll'
import Aboutus from '@/components/Aboutus'
import Rule from '@/components/Rule'
import Contactus from '@/components/Contactus'
import Index from '@/components/Index'

Vue.use(Router)

export default new Router({
  routes: [
    
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    { 
      path: '/aboutus',
      name: 'Aboutus',
      component: Aboutus

    },
    {
      path: '/contactus',
      name: 'Contactus',
      component: Contactus
    },
    {
      path: '/rule',
      name: 'Rule',
      component: Rule

    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin
    },
    {
      path: '/quiz',
      name: 'Quiz',
      component: Quiz,
      props: true
    },
    {
      path: '/poll',
      name: 'Poll',
      component: Poll,
      props: true
    }
  ]
})



