<template>
<div>
    
<div class="container">
        
<div id="mySidenav" class="sidenav">
      <a href="#" class="btn btn-success up" @click="like++"><i class="fa fa-thumbs-up" ></i>{{like}} LIKE </a>
    <a href="#" class="btn btn-primary down" @click="dislike++"><i class="fa fa-thumbs-down"></i>{{dislike}} DISLIKE </a>

    </div>
</div>
    </div>
</template>

<script>
export default {
data(){
    return{
        like: 10,
        dislike:1
    }
}
}
</script>

<style>

#mySidenav a {
    position: absolute;
    right: -70px;
    transition: 0.3s;
    padding: 15px;
    width: 149px;
    text-decoration: none;
    font-size: 20px;
    color: white;
    border-radius: 0 5px 5px 0;
    
}

#mySidenav a:hover {
    right: 0;
}

.up {
    margin-top: 220px;
    background-color: #4CAF50;
}

.down{
    margin-top: 280px;
    background-color: #2196F3;
}

</style>



