<template>
<div>
  
    <app-header></app-header>
    <app-menu></app-menu>
    <app-feedback></app-feedback>
  <div class="container">
  <h2>RULES</h2>
  <div class="alert alert-success">
    <p> We will provided 15 second or 20 second to the user to answer the question.</p>
  </div>
  <div class="alert alert-info">
    <p> User has to give response within given time period.</p>
  </div>
  <div class="alert alert-warning">
    <p>User have to enter the Poll code</p>
  </div>
</div>
</div>
</template>

<script>
import Header from './Header';
import Menu from './Menu';
import Feedback from './Feedback.vue';
export default {
components:{
  
       appHeader:Header,
        appMenu: Menu,
        appFeedback: Feedback

}
}
</script>

<style>
p{
            font-size:20px;
        }
        h1{
            font-family: algerian;
            text-align: center;
        }

</style>



