<template>
  <div class="custom-container">

      <div class="col-md-8">
        <div class="col-12 box" v-show="isComplete">
            <canvas id="quiz-result"></canvas>
        </div>
        <div class="col-12 box" v-if="!isComplete && game != ''">
            
            <loader v-if="showLoader"></loader>
            <div class="points">Score <span style="font-weight: 600;">{{score}}</span></div>
            <div class="round">Poll {{game.id}} of {{gamesList.length}}</div>
            <div class="timer">
                <div class="count">{{timer}}</div>
            </div>
            <div class="quiz">
                <div>
                    <div class="col-12 center">
                        <div class="col-md-10 question">
                            {{game.question}}
                        </div>
                    </div>

                    <div class="col-md-12 center">
                        <div class="row col-12 options">
                        <div class="col-md-5 option" v-for="(option, index) in game.options" :key="index" @click="submitAnswer(option.id)" :class="[(option.id == currentId) ? (option.id == correctId) ? 'correct': 'incorrect' : '',(option.id == correctId) ? 'correct': '']">
                            {{option.value}}
                        </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="progress" :style="{width: 100 - (timer/game.timer*100)+'%'}"></div>
        </div>
        <div class="col-12 box" v-if="!isComplete && game == ''">
            <loader></loader>
            <div style="text-align: center;">Get Ready</div>
        </div>
      </div>
  </div>
</template>

<script>

import axios from 'axios';
import Loader from './Loader';
import Chart from 'chart.js';
//import Tutorial from './Tutorial'

export default {
  props: ['quizData'],
  components:{
    Loader
   // Tutorial
  },
  data(){
      return{
          gamesList: [],
          game: "",
          result: [],
          timer: '',
          counter: '',
          showLoader: false,
          correct:0,
          inCorrect:0,
          notanswer:0,
          score:0,
          isComplete: false,
          correctId: '',
          currentId: '',
          loggedInIds: []

      }
  },
  methods:{
  	submitAnswer: function(id){
        clearInterval(this.counter);
        this.currentId = id;
        

        if(id != -1){
            this.correctId = this.game.answer;
        }
        
        if(id==-1){
            this.notanswer++;
        }
        else if(id == this.game.answer){
            this.correct++;
            this.score+=this.game.point;
        }
        else{
            this.inCorrect++;
        }
        let answer = "";
        let options = ["A", "B", "C", "D"];
        if(id == -1){
            answer = "Not Answer";
        }else{
            answer = options[id - 1];
        }
        let obj = {
            answer: answer
        }

        socket.emit('submitAnswer', obj);
        setTimeout(()=>{
            this.game = '';
            this.currentId = '';
            this.correctId = '';
        },1000);

    },
    countdown: function(){
        this.counter = setInterval(()=>{
            this.timer--;
        },1000);
    },
    createChart: function(chartId, chartData){

        const ctx = document.getElementById(chartId);
        const myChart = new Chart(ctx, {
            type: chartData.type,
            data: chartData.data,
            options: chartData.options
        });
    },

    finishQuiz: function(){
        this.result = [];
        this.result.push(this.correct);
        this.result.push(this.inCorrect);
        this.result.push(this.notanswer);

        let chartData = {
            type: 'pie',
            data: {
                labels: ["Correct","Incorrect","NotAnswer"],
                datasets: [
                    {
                        label: 'Quiz',
                        backgroundColor: ['#228B22','#ffce56','#36a2eb'],
                        data: this.result,
                        borderColor: 'white',
                        borderWidth: 1
                    }, 
                    ]
            },
            options: {
            responsive: true,
                title: {
                    display: true,
                    text: 'Result'
                },
                tooltips: {
                    mode: 'index',
                    intersect: true
                },
                scales: {
                    ticks: [{
                        beginAtZero:true
                    }]
                }
            }
        }


        this.createChart('quiz-result', chartData);

    }
  },

  created(){

        if(this.quizData == undefined){
            this.$router.push({name: 'Home'});
        }

        this.gamesList = this.quizData;

        socket.on('startPoll', (data)=>{
            this.isComplete = false;
            console.log(data);
            this.game = this.gamesList.filter((game)=>{
                return game.id == data.id;
            })[0];
            this.timer = this.game.timer;
            this.countdown();
        });

        socket.on('finishQuiz', (data)=>{
            this.isComplete = data;
            this.finishQuiz();
        });
        



  },
  watch:{
      'timer': function(){
          if(this.timer == 0){
              console.log("Done");
              this.submitAnswer(-1);
          }
      }
  }
}
</script>

<style scoped>

    .progress{
        transition: all 1s cubic-bezier(1, 1, 1, 1);
        height: 5px;
        width: 0%;
        background: #079b80;
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0;
        border-radius: 0;
    }
    .correct{
        background: green;
        color: #fff;
    }

    .incorrect{
        background: red;
        color: #fff;
    }

    .custom-container{
        display: flex;
        justify-content: center;
    }
    .box{
        /* width: 90%; */
        margin-top: 100px;
        min-height: 200px;
        border-radius: 3px;
        border: 1px solid #ccc;
        padding: 15px 20px 35px;
        -webkit-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        -moz-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
    }

    .round{
        text-align: center;
        font-weight: 600;
    }

    .timer{
        margin: 10px;
        display: flex;
        justify-content: center;
    }

    .timer .count{
        border: 1px solid #333;
        border-radius: 50%;
        height: 30px;
        width: 30px;
        line-height: 25px;
        text-align: center;
    }

    .center{
        display: flex;
        justify-content: center;
    }

    .options{
        display: flex;
        justify-content: space-around
    }

    .option{
        border: 1px solid #ccc;
        border-radius: 10px;
        min-height: 40px;
        /* line-height: 35px; */
        /* width: 45%; */
        margin: 10px;
        padding: 10px;
        cursor: pointer;
    }

    .question{
        font-weight: 600;
        margin-bottom: 30px;
        text-align: center;
    }
</style>



