<template>
<div>
<app-header></app-header>
<div class="custom-container">
  <div class="col-md-8">

    <div class="col-12 box" v-show="isComplete">
        <canvas id="quiz-result" style="height: 350px;"></canvas>
    </div>
    <div class="col-12 box" v-if="!isComplete && gamesList!=''" >

        <loader v-if="showLoader"></loader>
        <div class="points">Poll <span style="font-weight: 600;">{{poll}}</span></div>
        <div class="round">Quiz {{game.id}} of {{gamesList.length}}</div>
        <div class="timer">
            <div class="count">{{timer}}</div>
        </div>
        <div class="quiz">
            <div>
                <div class="col-12 center">
                <div class="col-md-10 question">
                    {{game.question}}
                </div>
                </div>

                <div class="col-md-12 center">
                <div class="row col-md-12 options">
                    <div class="col-md-5 option" v-for="(option, index) in game.options" :key="index" @click="submitAnswer(option.id)" :class="[(option.id == currentId) ? (option.id == correctId) ? 'correct': 'incorrect' : '',(option.id == correctId) ? 'correct': '']">
                        {{option.value}}
                    </div>
                </div>
                </div>
            </div>
        </div>
        <div class="progress" :style="{width: 100 - (timer/game.timer*100)+'%'}"></div>
        <div class="left-icon" @click="previous" :class="activeQuestion == 1 ? 'custom-disable' : ''">
            <i class="fa fa-chevron-left"></i>
        </div>
        <div class="right-icon" @click="next" :class="activeQuestion == gamesList.length ? 'custom-disable' : ''">
            <i class="fa fa-chevron-right"></i>
        </div>
    </div>
    <div class="col-12" style="margin-top: 10px; text-align: center;" v-if="gamesList!=''">
        <button class="btn btn-primary c-btn" @click="getPoll">Poll</button>
        <button class="btn btn-primary c-btn" @click="finish">Show Result</button>
    </div>

    <div class="col-12" style="margin-top: 10px; text-align: center;" v-else>
        

        <button class="btn btn-primary c-btn" @click="start">Start</button>
        <div class="col-12">
            <span v-for="(id, index) in loggedInIds" :key="index" class="user">
                {{id}} 
            </span>
        </div>

    </div>
  </div>
</div>
</div>
</template>

<script>

import axios from 'axios';
import Loader from './Loader';
import Chart from 'chart.js';
import Header from './Header';


export default {
  props: ['quizData'],
  components:{
    Loader,
       appHeader:Header,
  },
  data(){
      return{
          gamesList: "",
          game: "",
          result: [],
          timer: '',
          counter: '',
          showLoader: false,
          correct:0,
          inCorrect:0,
          notanswer:0,
          poll:0,
          isComplete: false,
          correctId: '',
          currentId: '',
          loggedInIds: [],
          activeQuestion: 1,
          pollAnswers: {}

      }
  },
  methods:{
    
    finish: function(){
        this.isComplete = !this.isComplete;
        this.drawChart();
        socket.emit('finishQuiz', this.isComplete);
    },
    start: function(){
        this.gamesList = this.quizData;
        this.game = this.gamesList.filter((game)=>{
            return game.id == 1;
        })[0];
        this.timer = this.game.timer;
    },
    getPoll: function(){

        this.poll = 0;
        this.pollAnswers = {};

        let options = ["A", "B", "C", "D"];
        for(let i=0; i< this.game.options.length; i++){
            this.pollAnswers[options[i]] = 0;
        }

        console.log(this.pollAnswers);

        let obj = {
            id : this.activeQuestion
        };
        socket.emit('startPoll', obj);

        this.drawChart();

        this.countdown();
    },
    previous: function(){
        this.poll = 0;
        let id = --this.activeQuestion;
        this.openQuestion(id);
    },
    next: function(){
        this.poll = 0;
        let id = ++this.activeQuestion;
        this.openQuestion(id);
    },

    openQuestion: function(id){
        clearInterval(this.counter);
        this.showLoader = true;
        setTimeout(()=>{

            this.showLoader = false;
            this.currentId = '';
            this.correctId = '';    
            if(id <= this.gamesList.length){
                this.game = this.gamesList.filter((game)=>{
                    return game.id == id;
                })[0];
                this.timer = this.game.timer;
            }


        },1000);
    },

  	submitAnswer: function(id){
        clearInterval(this.counter);
        this.currentId = id;
        

        if(id != -1){
            this.correctId = this.game.answer;
        }
        
        if(id==-1){
            this.notanswer++;
        }
        else if(id == this.game.answer){
            this.correct++;
            this.score+=this.game.point;
        }
        else{
            this.inCorrect++;
        }
        
    },
    countdown: function(){
        this.counter = setInterval(()=>{
            this.timer--;
        },1000);
    },
    createChart: function(chartId, chartData){

        const ctx = document.getElementById(chartId);
        const myChart = new Chart(ctx, {
            type: chartData.type,
            data: chartData.data,
            options: chartData.options
        });
    },

    drawChart: function(){
        this.result = [];
        this.result.push(this.correct);
        this.result.push(this.inCorrect);
        this.result.push(this.notanswer);  

        console.log(this.pollAnswers);

        let chartData = {
            type: 'bar',
            data: {
                labels: Object.keys(this.pollAnswers),
                datasets: [
                    {
                        label: 'Quiz',
                        backgroundColor: '#ff6953',
                        data: Object.keys(this.pollAnswers).map(k => this.pollAnswers[k]),
                        borderColor: 'white',
                        borderWidth: 1
                    }, 
                    ]
            },
            options: {
            responsive: true,
                title: {
                    display: true,
                    text: 'Result'
                },
                tooltips: {
                    mode: 'index',
                    intersect: true
                },
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero:true
                        }
                    }]
                }
            }
        }


        this.createChart('quiz-result', chartData);

    }
  },

  created(){

        if(this.quizData == undefined){
            this.$router.push({name: 'Admin'});
        }


        socket.on('addUser', (data)=>{
            this.loggedInIds.push(data);
        });


        socket.on('submitAnswer', (data)=>{
            if(this.pollAnswers[data.answer]){
                this.pollAnswers[data.answer]++;
            }else{
                this.pollAnswers[data.answer] = 1;
            }
            console.log(this.pollAnswers);

            this.poll++;

        });



  },
  watch:{
      'timer': function(){
          if(this.timer == 0){
              console.log("Done");
              this.submitAnswer(-1);
          }
      }
  }
}
</script>

<style scoped>

    .custom-disable{
        pointer-events: none;
        opacity: 0.5;
    }
    
    .left-icon{
        position: absolute;
        left: 15px;
        top: calc(50% - 25px);
        font-size: 25px;
        cursor: pointer;
    }

    .right-icon{
        position: absolute;
        right: 15px;
        top: calc(50% - 25px);
        font-size: 25px;
        cursor: pointer;
    }

    .user{
        background: aliceblue;
        padding: 5px;
        border: 1px solid;
        margin: 3px;
    }

    .c-btn{
        border-radius: 0px;
        min-width: 100px;
        margin: 5px;
    }

    .progress{
        transition: all 1s cubic-bezier(1, 1, 1, 1);
        height: 5px;
        width: 0%;
        background: #079b80;
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0;
        border-radius: 0;
    }
    .correct{
        background: green;
        color: #fff;
    }

    .incorrect{
        background: red;
        color: #fff;
    }

    .custom-container{
        display: flex;
        justify-content: center;
    }
    .box{
        /* width: 90%; */
        margin-top: 100px;
        min-height: 200px;
        border-radius: 3px;
        border: 1px solid #ccc;
        padding: 15px 20px 35px;
        -webkit-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        -moz-box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
        box-shadow: 7px 9px 22px -9px rgba(0,0,0,0.63);
    }

    .round{
        text-align: center;
        font-weight: 600;
    }

    .timer{
        margin: 10px;
        display: flex;
        justify-content: center;
    }

    .timer .count{
        border: 1px solid #333;
        border-radius: 50%;
        height: 30px;
        width: 30px;
        line-height: 25px;
        text-align: center;
    }

    .center{
        display: flex;
        justify-content: center;
    }

    .options{
        display: flex;
        justify-content: space-around
    }

    .option{
        border: 1px solid #ccc;
        border-radius: 10px;
        min-height: 40px;
        /* line-height: 35px; */
        /* width: 45%; */
        margin: 10px;
        padding: 10px;
        cursor: pointer;
    }

    .question{
        font-weight: 600;
        margin-bottom: 30px;
        text-align: center;
    }
</style>



