 <template>
    <div class="container">
<nav class="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
      <a class="navbar-brand" href="#">Live Polling</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarsExampleDefault">
        <ul class="navbar-nav mr-auto">
          
              <router-link to ="/" tag="li" active-class="active" exact><a class="nav-link">Home</a></router-link>
              <router-link to ="/aboutus" tag="li" active-class="active"><a class="nav-link">About Us</a></router-link>
              <router-link to ="/contactus" tag="li" active-class="active"><a class="nav-link">Contact Us</a></router-link>
              </ul>
            <ul class="nav navbar-nav navbar-right">
              <router-link to ="/home" tag="li" active-class="active"><a class="nav-link">Poll</a></router-link>
              <router-link to ="/admin" tag="li" active-class="active"><a class="nav-link">Login</a></router-link>
              
             
          
        </ul>
        <form class="form-inline my-2 my-lg-0">
          <input class="form-control mr-sm-2" type="text" placeholder="Search" aria-label="Search">
          <button class="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
        </form>
      </div>
    </nav>

</div>
</template>

<script>
export default {
  directives: {
            'local-highlight': {
                bind(el, binding, vnode) {
                    var delay = 0;
                    if (binding.modifiers['delayed']) {
                        delay = 3000;
                    }
                    if (binding.modifiers['blink']) {
                        let mainColor = binding.value.mainColor;
                        let secondColor = binding.value.secondColor;
                        let currentColor = mainColor;
                        setTimeout(() => {
                            setInterval(() => {
                                currentColor == secondColor ? currentColor = mainColor : currentColor = secondColor;
                                if (binding.arg == 'background') {
                                    el.style.backgroundColor = currentColor;
                                } else {
                                    el.style.color = currentColor;
                                }
                            }, binding.value.delay);
                        }, delay);
                    } else {
                        setTimeout(() => {
                            if (binding.arg == 'background') {
                                el.style.backgroundColor = binding.value.mainColor;
                            } else {
                                el.style.color = binding.value.mainColor;
                            }
                        }, delay);
                    }
                }
            }
        }
}
</script>

<style scoped>
.navbar{
    border-radius: 40px;
    margin-top: 50px;
}
</style>




